  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
   
    background-color: #666666;
    min-width: 160px;
    /* box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2); */
    z-index: 1;
    position: absolute;
    font-size: 14px;
    
  }
  
  .drp {
    color: rgb(255, 255, 255);
    padding: 10px 14px;
    text-decoration: none;
    display: block;
  }
  

.dropdown:hover .dropdown-content {display: block;}
.dropdown:active .dropdown-content {display: block;}

.dropdown-content :hover{
    background-color: rgb(61, 61, 61);
}
.dropdown-content:active{
    background-color: rgb(61, 61, 61);
}




.product_card{
    max-width: 14rem;
    overflow: hidden;
    height: 20rem;
    padding: 5px;
    box-shadow:  0 0 5px #ddd;
    /* margin:35px 0; */
    position: relative;
   
    
}
.product_card img{
    width: 90%;
    height: 150px;
    display: block;
    object-fit: cover;
    border-radius: 10px;
    
    align-items: center;
    margin-left: 5%;
    padding: 5px;
}

.product_box h5{
    width: 100%;
    /* text-overflow: ellipsis;
    overflow: hidden; */
    font-size: 18px;
   
    text-transform: capitalize;
    cursor: pointer;
    color: #323232;
    margin-top: 5px;
    font-weight: 500;
    height: 47px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

}
.product_box p{
    
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
        
        white-space: nowrap;
        text-transform: capitalize;
        cursor: pointer;
        color: #323232;
        margin-top: 5px;
    
}
.product_card span{
    color: rgb(1, 40, 77);
    font-weight: 500;
    
}
.product_box p{
    width: 100%;
    /* display: -webkit-box;
