/* -------   Required CSS (customizable)  ------- */
/* ---------------------------------------------- */

:root {
  --color__accent: #6200ee;

  --field__background: #f5f5f5;
  --field__background--hover: #e9e9e9;
  --input__border: rgba(0, 0, 0, 0.42);
  --input__border--hover: #6200ee;
  --label__color: #222;

  --font__size: 16px;
  --font__family: Roboto, Arial, sans-serif;
}

.floating {
  
  margin: 15px;
  background-color: #fff;
  transition: background-color 0.2s ease;
  border-top-left-radius: 4px;
  width: 300px;
  border-top-right-radius: 4px;
}

.floating:hover,
.floating:focus-within {
  background-color: #e9e9e9;
  background-color: var(--field__background--hover);
}

.big_floating {
    margin: 15px;
    background-color:#fff;
    transition: background-color 0.2s ease;
    border-top-left-radius: 4px;
    width: 635px;
    border-top-right-radius: 4px;
} 

input {
    outline: none;
}

.floating__input {
  padding: 1.8rem 1rem 0.6rem;
  font-size: 1rem; 
  border : 1px solid rgba(0,0,255,0.25);
  width: 100%;
  height: 100%;
  transition: border-color  0.2s ease;
 
}

.floating:hover .floating__input {
  border-color: #6200ee;
  border-color: var(--input__border--hover);
}

.floating__input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0);
}

.floating__input::placeholder {
  color: rgba(0, 0, 0, 0);
}

.floating__label {
  display: block;
  position: relative;
  max-height: 0;
  color: black;
  font-weight: 500;
  pointer-events: none;
}

.floating__label::before {
  color: #222;
  color: var(--label__color);
  content: attr(data-content);
  display: inline-block;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform-origin: left top;
  transition: transform 0.2s ease;
  top:-10px;
  left: 1rem;
  position: relative;
}

.floating__label::after {
  bottom: 1rem;
  content: "";
  height: 0.1rem;
  position: absolute;
  transition: transform 180ms cubic-bezier(0.4, 0, 0.2, 1),
  opacity 180ms cubic-bezier(0.4, 0, 0.2, 1), background-color 0.3s ease;
  opacity: 0;
  left: 0;
  top: 100%;
  margin-top: -0.1rem;
  transform: scale3d(0, 1, 1);
  width: 100%;
 
}

.floating__input:focus + .floating__label::after {
  transform: scale3d(1, 1, 1);
  opacity: 1;
}

.floating__input:-ms-input-placeholder + .floating__label::before {
  transform: translate3d(0, -2.2rem, 0) scale3d(1, 1, 1);
}

.floating__input:placeholder-shown + .floating__label::before {
  transform: translate3d(0, -2.2rem, 0) scale3d(1, 1, 1);
}

.floating__label::before,
.floating__input:focus + .floating__label::before {
  transform: translate3d(0, -3.12rem, 0) scale3d(0.82, 0.82, 1);
}

.floating__input:focus + .floating__label::before {
  color: #6200ee;
  color: var(--color__accent);
}

